import React from "react";
import Layout from '../../../components/layout'
import SEO from "../../../components/seo"

const seo = {
   title: "Compounding over time",
   description: "",
}

const roundValue = (val, places = 2) => {
   const num = Math.pow(10, places);
   return Math.round(val * num) / num;
}

class StocksInvestPage extends React.Component {
   
   constructor() {
      super();
      this.commaRegex = /,/g
      this.state = {
         monthlyInput: 100,
         averageYield: 5.00,
      }
   }

   changeMonthlyInput = (e) => {
      let val = e.target.value;
      val = val.replace(this.commaRegex, '.')
      this.setState({ monthlyInput: val })
   }
   changeAverageYield = (e) => {
      let val = e.target.value;
      val = val.replace(this.commaRegex, '.')
      this.setState({ averageYield: val })
   }

   computeYear = (arr, year) => {
      const av = (+this.state.averageYield + 100) / 100
      const lastYear = arr[arr.length - 1];
      const myNextInput = this.state.monthlyInput * 12;
      const nextBalance = (lastYear.balance + myNextInput) * av;
      const nextMyTotalInput = lastYear.myTotalInput + myNextInput;
      const nextGrow = nextBalance - nextMyTotalInput;
      arr.push({
         balance: roundValue(nextBalance),
         myTotalInput: roundValue(nextMyTotalInput),
         grow: roundValue(nextGrow),
      })
      return arr;
   }

   computeYield = () => {
      const av = (+this.state.averageYield+100) / 100;
      let arr = [{
         balance: roundValue(this.state.monthlyInput * 12 * av),
         myTotalInput: roundValue(this.state.monthlyInput * 12),
         grow: roundValue(this.state.monthlyInput * 12 * av - this.state.monthlyInput * 12),
      }];

      for (let i = 1; i < 30; i++) {
         arr = this.computeYear(arr);
      }
      
      return arr;
   }

   percentFrom(smallerValue, biggerValue) {
      return roundValue(smallerValue * 100 / biggerValue)
   }

   render() {
      return (
         <Layout>
            <SEO 
               {...seo}
            />
            <div className="topbottom_margined">Average yield in %<input className="margin-left" onChange={this.changeAverageYield} value={this.state.averageYield} /></div>
            <div>Monthly contribution <input className="margin-left" onChange={this.changeMonthlyInput} value={this.state.monthlyInput} /></div>
            <table>
               <thead>
                  <tr>
                     <th>Year</th>
                     <th>Value</th>
                     <th>My total contribution</th>
                     <th>Total Grow</th>
                  </tr>
               </thead>
               {this.computeYield().map((obj, i) => (
                  <tr>
                     <td>{i} Year</td>
                     <td>{obj.balance} €</td>
                     <td>{obj.myTotalInput} € <em>({this.percentFrom(obj.myTotalInput, obj.balance)} %)</em></td>
                     <td>{obj.grow} € <em>({this.percentFrom(obj.grow, obj.balance)} %)</em></td>
                  </tr>
               ))}
            </table>
         </Layout>
      )
   }
}

export default StocksInvestPage;